/* Estilo para o corpo e o título */
body {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  background-color: #f4f4f9;
}

h1 {
  color: #28a745; /* Verde */
  font-size: 32px;
  margin-bottom: 20px;
}

/* Estilo para o input de área */
.input-area {
  padding: 10px;
  margin-right: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 20px;
}

/* Estilo para o botão */
button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}

/* Estilo para a área que exibe as emissões */
.total-emissoes {
  margin-top: 20px;
  font-size: 18px;
  color: #333;
}

.total-emissoes p {
  margin: 5px 0;
}

.total-emissoes span {
  font-weight: bold;
}

/* Estilo para a área dos gráficos */
.grafico-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 0 10px;
  flex-wrap: wrap;
}

/* Estilo para cada gráfico */
.grafico {
  flex: 1 1 calc(50% - 20px);
  height: 400px;
  min-width: 300px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Títulos dos gráficos */
.grafico h3 {
  font-size: 18px;
  color: #28a745; /* Verde */
  text-align: center;
  margin-bottom: 10px;
}

/* Texto explicativo das barras */
.barra-info {
  margin-top: 20px;
  font-size: 16px;
  text-align: center;
  color: #333;
}
